.sendForm {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    align-items: center;
}

.textarea {
    overflow: hidden;
    width: 80%;
    height: 40px;
    padding: 10px;
    font-size: 16px;
    border-radius: 10px;
    resize: none;
    box-sizing: border-box;
}

.button {
    width: 15%;
    height: 40px;
    font-size: 16px;
    border-radius: 20px;
    border: none;
    background-color: #0066cc;
    color: #FFFFFF;
}

